.feedback-container .text-area 
    position: relative
    border: 1px solid #ccd0d4
    box-shadow: 0 1px 1px rgba(0,0,0,.04)
    background: #fff
    padding: 10px

.feedback-author
  ul
    padding: 0
    margin: 0
    display: flex
    align-items: center
    li
      display: inline-block
      margin: 0
      margin-inline-end: 10px

.postbox-container 
    margin-top: 6px

.feedback-author .avatar 
    border-radius: 5px

.text-area 
    margin-block-end: 10px

.feedback-container p 
    margin: 0
    font-size: 15px


th.column-status 
    width: 120px


.column-screenshot 
    width: 160px

.column-status span 
    display: inline-block
    padding: 10px
    font-weight: bold
    text-align: center
    width: 90%
    color: #fff

.screenshot img 
    width: 100%

.column-status span.open 
    background-color: grey

.column-status span.review 
    background-color: purple

.column-status span.in_progress 
    background-color: orange 
    color: #000

.column-status span.closed 
    background-color: green


.feedback-container .screenshot 
    text-align: center


.browser-info .label 
    font-weight: bold
    display: inline-block
    width: 90px


